<template>
  <div id="login_content">
    <h2 class="mb-3" >가입 정보</h2>

    <!-- 영어로 시작해서 숫자만 조합 가능 -->
    <b-input
      v-validate="{ required: true, regex: '(^(?=.*[a-zA-z])|^(?=.*[a-zA-z])(?=.*[0-9])).{5,20}$' }"
      :class="{ input: true, 'is-danger': errors.has('userId') }"
      v-model="userId"
      name="userId"
      type="text"
      placeholder="아이디"
    />
    <p v-show="errors.has('userId')" style="color: red;">아이디를 입력해주세요.</p>
    <b-button @click="toCheck" class="btn-pers">중복 확인</b-button>

    <hr>
    <!-- 대문자 소문자 숫자 특수문자가 포함된 8자이상 16자로 입력 -->
    <b-input
      v-validate="{ required: true, regex: '^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+]).{8,16}$' }"
      :class="{ input: true, 'is-danger': errors.has('userPassword') }"
      v-model="userPassword"
      name="userPassword"
      type="password"
      placeholder="패스워드"
      ref="userPassword"
    />
    <em v-show="errors.has('userPassword')" style="color: red;">패스워드를 다시 입력해주세요.</em>

    <!-- 패스워드 확인-->
    <b-input
      v-validate="'required|confirmed:userPassword'"
      :class="{'is-danger': errors.has('userPasswordCheck')}" data-vv-as="userPasswordCheck"
      v-model="userPasswordCheck"
      name="userPasswordCheck"
      type="password"
      placeholder="패스워드 확인"
    />
    <p v-show="errors.has('userPasswordCheck')" style="color: red;">패스워드가 다릅니다.</p>

    <!-- 영어 한글만 조합 가능 -->
    <hr>
    <b-input
      v-validate="{ required: true, regex: '(?=.*[a-zA-z])|(?=.*[ㄱ-ㅎ|ㅏ-ㅣ|가-힣])' }"
      v-model="userName"
      placeholder="이름"
      name="userName"
      @input="changeName"
    />
    <p v-show="errors.has('userName')" style="color: red;">이름을 다시 입력하시오.</p>

    <!-- 전화번호 xxx-xxxx-xxxx 포맷 -->
    <hr>
    <b-input
      v-validate="{ required: true, regex: '^([0-1]{3}-[0-9]{4}-[0-9]{4})$' }"
      v-model="userPhonenumber"
      placeholder="전화번호"
      name="userPhonenumber"
    />
    <span v-show="errors.has('userPhonenumber')" style="color: red;">전화번호 양식이 아닙니다.(xxx-xxxx-xxxx)</span>

    <!-- 이메일형식 -->
    <hr>
    <b-input 
      v-validate="'required|email'"
      :class="{ input: true, 'is-danger': errors.has('email') }"
      v-model="userEmail"
      name="email"
      type="text"
      placeholder="이메일"
    />
    <p v-show="errors.has('email')" style="color: red;">이메일 양식이 아닙니다.</p>

    
    <!-- 한글 영어 숫자만 조합 가능 -->
    <hr>
    <b-input
      v-validate="{ required: true, regex: '(?=.*[a-zA-z])|(?=.*[ㄱ-ㅎ|ㅏ-ㅣ|가-힣])|(?=.*[0-9])' }"
      v-model="userNickname"
      placeholder="닉네임"
      name="userNickname"
      @input="changeNickName"
    />
    <p v-show="errors.has('userNickname')" style="color: red;">특수문자는 들어갈 수 없습니다.</p>


    <b-button type="submit" class="mt-4 btn-pers" id="login_button" @click="signSubmit" >
      회원가입
    </b-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userId: null,
      userPassword: "asd13795",
      userName: null,
      userPhonenumber: null,
      userEmail: null,
      userNickname: null,
      userPasswordCheck: null,
    }
  },
  methods: {
    signSubmit() {
      this.$validator.validateAll().then((result) => {
        if(result) {
          let saveData = {};
          saveData.userId = this.userId;
          saveData.userPassword = this.userPassword;
          saveData.userName = this.userName;
          saveData.userPhonenumber = this.userPhonenumber;
          saveData.userEmail = this.userEmail;
          saveData.userNickname = this.userNickname;
          try {
            this.$axios.post("http://100.0.0.157:20200/user/create", JSON.stringify(saveData), {
              headers: {
                "Content-Type" : `application/json`,
              },
              body: {
                userId : this.userId,
                userPassword : this.userPassword,
                userName : this.userName,
                userPhonenumber : this.userPhonenumber,
                userNickname : this.userNickname,
                userEmail : this.userEmail,
                userPermission : 0,
              }
          })
          .then((res) => {
            if (res.status == 200) {
              alert("Sign Up");
              if(this.$store.state.userpermission == 0){
                this.$router.push("/");
              } else {
                this.$router.push("/userlist");
              }

            }
          });
          } catch (error) {
            console.error(error);
          }
          return
        }
        alert("다시 입력해주세요.")

      })
    },
    toCheck() { // 아이디 중복 체크

    },

  },
}
</script>