<template>
  <div>
    
    <div id="login_content">

        <h2 class="mb-3">로그인</h2>
        <div>
          <label for="id">아이디</label>
          <input
            class="form-control"
            type="text"
            maxlength="20"
            v-model="userId" 
            placeholder="id"
            
          />
          <!-- v-model을 통해 바로 데이터 넣기 -->
        </div>
        <div class="input">
          <label for="password">패스워드</label>
          <input
            class="form-control"
            type="password"
            maxlength="20"
            v-model="userPassword"
            placeholder="password"
          />
        </div>

        <button type="submit" class="mt-4 btn-pers" id="login_button" @click="loginSubmit" >
          로그인
        </button>

        <div class="alternative-option mt-4">
          <span @click="moveToSignUp">회원가입</span>
        </div>
    </div>
  </div>
</template>


<script>
// const HOST = "http://localhost:8080/user"

  export default {
    data() {
      return {
        userId : null,
        userPassword : null,
      }
    },

    methods: {
      loginSubmit() {

        let saveData = {};
        saveData.userId = this.userId;
        saveData.userPassword = this.userPassword;

        try {
          this.$axios.post("http://100.0.0.157:20200/user/login", JSON.stringify(saveData), {
            headers: {
              "Content-Type" : `application/json`,
            },
            body: {
              userId : this.userId,
              userPassword : this.userPassword
            }
          })
          .then((res) => {
            if (res.data == true) {

              this.$axios.get("http://100.0.0.157:20200/user/readone/" + this.userId).then((res)=>{
                this.$store.commit('setUserid', res.data.userId);
                this.$store.commit('setNickname', res.data.userNickname);
                this.$store.commit('setPermission', res.data.userPermission);
                this.$store.commit('setIndex', res.data.id);
                alert(localStorage.getItem("userNickname") + "님 안녕하세요");
                this.$router.push("/boardlist"); 
              })
              //this.$router.push("/boardlist"); 제일 빠르게 실행됨
            }
            else {
              alert("아이디가 없거나 패스워드가 틀렸습니다.");
            }
          });
        } catch (error) {
          console.log(error.response);
        }
      },

      moveToSignUp() {
        this.$router.push("/signup");
      },

    },

    computed: {
      
    }

  }

</script>