import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    userid: localStorage.getItem('userId') || '',
    usernickname: localStorage.getItem('userNickname') || '',
    userpermission: localStorage.getItem('userPermission') || '',
    userindex: localStorage.getItem('userIndex') || '',
    boardid: '',
  },

  //userid 가져오기
  // getters: {
  //   userinfo: function(state) {
  //     return state.userid;
  //   }
  // },

  //로그인된 userid 저장
  mutations: {
    setUserid(state, userid) {
      state.userid = userid;
      localStorage.setItem('userId', userid)
    },
    setNickname(state, usernickname) {
      state.usernickname = usernickname;
      localStorage.setItem('userNickname', usernickname)
    },
    setPermission(state, userpermission) {
      state.userpermission = userpermission;
      localStorage.setItem('userPermission', userpermission)
    },
    setIndex(state, userindex) {
      state.userindex = userindex;
      localStorage.setItem('userIndex', userindex)
    },
    setBoardid(state, boardid) {
      state.boardid = boardid;
    }
  },
})