<template>
  <div style="text-align: center">
    <h1>Board Create</h1>
    <b-card 
      style ="width: 1000px; display:inline-block">
      <b-card-title>
        TITLE
        <input 
          class="form-control"
          type="text"
          maxlength="20"
          v-model="boardTitle"/> 
      </b-card-title>
      <hr>
        CONTENTS
        <input 
        class="form-control"
        type="text"
        maxlength="20"
        v-model="boardContents"/>
      <hr>
      <button type="submit" class="mt-4 btn-pers" id="login_button" @click="boardCreate" >
        게시글 작성
      </button>
    </b-card>
  </div>

</template>

<script>
export default {
  data() {
    return {
      boardTitle: this.$route.params.index,
      boardContents: null,
      userNickname: this.$store.state.usernickname
    }
  },
  methods: {
    boardCreate() {
    let saveData = {};
      saveData.boardTitle = this.boardTitle;
      saveData.boardContents = this.boardContents;
      saveData.userNickname = this.userNickname;

      try {
        this.$axios.post("http://100.0.0.157:20200/board/create", JSON.stringify(saveData), {
          headers: {
            "Content-Type" : `application/json`,
          },
          body: {
            boardTitle : this.boardTitle,
            boardContents : this.boardContents,
            userNickname : this.userNickname,
          }
        })
        .then((res) => {
          if (res.status == 200) {
            alert("게시글 작성 완료");
            this.$router.push({name: "boardInfo", params: {id: res.data.id}});
          }
        });
      } catch (error) {
        console.error(error);
      }
    },
  }
      
}



</script>