<template>
  <div style="text-align: center">
    <h1>게시글 수정</h1>
    <b-card 
      style ="width: 1000px; display:inline-block">
      <b-card-title>
        제목
        <input 
          class="form-control"
          type="text"
          maxlength="20"
          v-model="boardTitle"/> 
      </b-card-title>

      <hr>
        내용
        <input 
        class="form-control"
        type="text"
        maxlength="20"
        v-model="boardContents"/>
      <hr>
      <button type="submit" class="mt-4 btn-pers" id="login_button" @click="boardUpdate" >
        수정
      </button>
      <b-button id="login_button" class="mt-4 btn-pers" @click="toBack">취소</b-button>
    </b-card>
  </div>

</template>

<script>
export default {
  data() {
    return {
      boardId: this.$route.params.id,
      boardTitle: "",
      boardContents: "",
    }
  },
  methods: {
    getBoard() {
      this.$axios.get("http://100.0.0.157:20200/board/read/" + this.boardId).then((res) => {
        this.boardTitle = res.data.boardTitle
        this.boardContents = res.data.boardContents
        console.log(res.data)
      })
      .catch((error) => {
        console.log(error);
      })
    },

    boardUpdate() {
      const formData = new FormData();
      formData.append("id", this.boardId)
      formData.append("boardTitle" , this.boardTitle)
      formData.append("boardContents" , this.boardContents)

      try {
        this.$axios.put("http://100.0.0.157:20200/board/update", formData).then((res)=> {
          this.$router.push({name: 'boardInfo' , params: {id: res.data.id}})
        }) 
        } catch(error) {
            console.log(errror)
      }
    },

    toBack(){
      this.$router.go(-1)
    }

  },
  mounted() {
    getBoard()
  }
      
}
</script>