<template>

  <div style="text-align: center">
    <h1>{{ user.userNickname }}님의 정보 수정 </h1>
    <b-card 
      style ="width: 400px; display:inline-block">
      
      <b-card-title>이름</b-card-title>
      <div class="form-control" v-if="userNameOn==1">{{ user.userName }}</div>
      <b-input 
        v-validate="{ required: true, regex: '(?=.*[a-zA-z])|(?=.*[ㄱ-ㅎ|ㅏ-ㅣ|가-힣])' }"
        :class="{ input: true, 'is-danger': errors.has('userName') }"
        type="text"
        v-model="userName"
        @input="changeName"
        name="userName"
        v-if="userNameOn==0"
      />
      <span v-show="errors.has('userName')" style="color: red;">이름을 다시 입력하시오.</span> <br>
      <b-button :pressed.sync="userNameOn">이름 수정</b-button>

      <hr>
      <label>패스워드</label>
      <br>
      <b-input 
        v-validate="{ required: true, regex: '^(?=.*[a-z])(?=.*[A-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+]).{8,16}$' }"
        :class="{ input: true, 'is-danger': errors.has('userPassword') }"
        name="userPassword"
        type="password"
        v-model="userPassword"
        placeholder="패스워드"
        ref="userPassword"
      />
      <span v-show="errors.has('userPassword')" style="color: red;">패스워드를 다시 입력하시오.</span>

      <b-input
      v-validate="'required|confirmed:userPassword'"
      :class="{'is-danger': errors.has('userPasswordCheck')}" data-vv-as="userPasswordCheck"
      v-model="userPasswordCheck"
      name="userPasswordCheck"
      type="password"
      placeholder="패스워드 확인"
      />
      <span v-show="errors.has('userPasswordCheck')" style="color: red;">패스워드가 다릅니다.</span>

      <hr>
      <b-card-title>NICKNAME</b-card-title>
      <b-input
        v-validate="{ required: true, regex: '(?=.*[a-zA-z])|(?=.*[ㄱ-ㅎ|ㅏ-ㅣ|가-힣])|(?=.*[0-9])' }"
        v-model="userNickname"
        placeholder="닉네임"
        name="userNickname"
      />
      <span v-show="errors.has('userNickname')" style="color: red;">잘못된 양식입니다.</span>

      <hr>
      <b-card-title>EMAIL</b-card-title>
      <b-input
        v-validate="'{required|email}'"
        :class="{ input: true, 'is-danger': errors.has('userEmail') }"
        type="text"
        v-model="userEmail"
        name="userEmail"
        placeholder="이메일"
      />
      <span v-show="errors.has('userEmail')" style="color: red;">잘못된 양식입니다.</span>


      <hr>
      <b-card-title>PHONE NUMBER</b-card-title>
      <b-input 
        type="text"
        maxlength="20"
        v-model="userPhonenumber"
        name="userPhonenumber"
        placeholder="전화번호"
      />
      <span v-show="errors.has('userPhonenumber')" style="color: red;">잘못된 양식입니다.</span>

      <hr>
      <b-form-radio v-model="userPermission" :aria-describedby="ariaDescribedby" name="some-radios" value=1 v-if="$store.state.userpermission==1">관리자</b-form-radio>
      <b-form-radio v-model="userPermission" :aria-describedby="ariaDescribedby" name="some-radios" value=0 v-if="$store.state.userpermission==1">사용자</b-form-radio>
      <v-btn class="me-4" type="submit" @click="userUpdate">UPDATE</v-btn>
    </b-card>

  </div>

</template>


<script>
export default {
  data() {
    return {
      user: [],
      id: this.$route.params.index || this.$store.state.userindex,  //리스트를 통해 들어왔을 경우 || 유저가 직접 들어왔을 경우
      userName: "",
      userPassword: "",
      userPasswordCheck: "",
      userEmail: "",
      userNickname: "",
      userPhonenumber: "",
      userPermission: 0,
    }
  },
  methods: {
    getUser(){
      this.$axios.get("http://100.0.0.157:20200/user/read/" + this.id).then((res) => {
        this.user = res.data
        this.userName = res.data.userName
        this.userPhonenumber = res.data.userPhonenumber
        this.userNickname = res.data.userNickname
        this.userEmail = res.data.userEmail
        this.userPermission = res.data.userPermission
        console.log(res.data)
      })
      .catch((error) => {
        console.log(error);
      })
    },
    userUpdate() {
      this.$validator.validateAll().then((result) => { // 모든 입력값이 오류가 뜨지 않는다면 post
        if(result) {
          const formData = new FormData();
          formData.append("id", this.user.id)
          formData.append("userPassword" , this.userPassword)
          formData.append("userName" , this.userName)
          formData.append("userPhonenumber" , this.userPhonenumber)
          formData.append("userEmail" , this.userEmail)
          formData.append("userNickname" , this.userNickname)
          formData.append("userPermission", this.userPermission)

          this.$axios.put("http://100.0.0.157:20200/user/update", formData).then((res)=> {

            if (this.$store.state.userpermission == '1') {
              this.$router.push({name: "userInfo", params: {id: this.user.id}}); //userInfo로 넘어갈 때 user.id 값까지 포함하여 가져감

            } else if(this.$store.state.userpermission == '0') {
              this.$store.commit('setNickname', this.userNickname);
              this.$store.commit('setPermission', this.userPermission);
              this.$router.push("/userinfo");
            }
          })
        }
        alert("다시 입력해주세요.")
      })
      
    }
  },
  mounted() {
    this.getUser();
  },
};
</script>