<template >
  <div style="background-color: #FFFFFF;">
    <div>
      <b-navbar toggleable="lg" v-if="$store.state.userid != ''" style="background-color: #ACA9BB;" >
        <b-navbar-brand href="/boardlist">HOME</b-navbar-brand>
        <b-navbar-brand href="/userlist" v-if="$store.state.userpermission == 1">USER</b-navbar-brand>
        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
        <b-collapse id="nav-collapse" is-nav>
          <!-- Right aligned nav items -->
          <b-navbar-nav class="ml-auto">
            <b-nav-item-dropdown right>
              <!-- Using 'button-content' slot -->
              <template #button-content>
                <em>{{ $store.state.usernickname }}님</em> 
              </template> 
              <b-dropdown-item @click="toProfile">Profile</b-dropdown-item>
              <b-dropdown-item @click="toLogout">Sign Out</b-dropdown-item>
            </b-nav-item-dropdown>
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </div>
    <router-view/>  
  </div>
</template>

<script>
export default {
  data() {
    return {
      usernickname: localStorage.getItem("userNickname"),
      userid: localStorage.getItem("userId")
    }
  },

  methods: {
    toProfile() {
      this.$router.push({name: 'userInfo'})
    },
    toLogout() {
      localStorage.clear();
      alert(this.$store.state.usernickname + "님이 로그아웃 했습니다.");
      this.$store.commit('setUserid', '');
      this.$store.commit('setNickname', '');
      this.$store.commit('setPermission', '');
      this.$store.commit('setIndex', '');
      this.$router.push("/")
    },
  },
}

</script>