<template >
  <div style="margin-top: 100px; margin-left: 5%; margin-right: 5%; border: 1px solid lightgray; border-radius: 10px; text-align: center" >
    <h1>게시판 목록</h1>

    <div style=" width: 300px; display:inline-block">
			<b-form-input
				id="filter-input"
				v-model="filter"
				type="search"
				placeholder="검색"
			/>
		</div>
    <br>

    <b-table 
      striped hover 
      :items="boardList"
      :per-page="perPage"
      :current-page="currentPage"
      sort-icon-left
      @row-clicked="toBoard"
      :fields="fields"
      :filter="filter"
      >
      <template #cell(index)="data">
        <router-link :to="{name: 'boardInfo', params: {id: data.index.id},}">
        </router-link>
        {{ data.index + 1 }}
      </template>
    </b-table>
    <b-pagination
      v-model="currentPage"
      :total-rows="rows"
      :per-page="perPage"
      aria-controls="my-table"
    ></b-pagination>
    <hr>
    <b-button @click ="toCreate">게시글 작성</b-button>
  </div>
    
</template>

<script>
export default {
  data() {
    return {
      boardList: [],
      perPage: 10,
      currentPage: 1,
      sortDesc: true,
      fields: [
        'index',
        {key: 'boardTitle'},
        {key: 'boardTime'},
        {key: 'userNickname'}
      ],
      filter: '',
    }
  },
  methods: {
    board() {
      this.$axios.get("http://100.0.0.157:20200/board/list").then((res) => {
        if (res.status == 200) {
          this.boardList = res.data;
          console.log(res.data)
        }
      })
    },
    toCreate() {
      this.$router.push("/boardcreate");
    },
    toBoard(index) {
      this.$store.commit('setBoardid', index.id)
      this.$router.push({name: 'boardInfo', params: {id: index.id}})
    },
  },
  mounted() {
    this.board();
  },
  computed: {
    rows() {
      return this.boardList.length;
    }
  }
}
</script>