<template>

  <div style="text-align: center">
    <h1>{{ user.userNickname }}님의 정보</h1>
    <b-card 
      style ="width: 400px; display:inline-block;"
    >
      <!-- 간단하게 userinfo에서 변경가능하도록 -->
      <!-- 아이디는 변경 불가 -->
      <b-card-title>
        <label>아이디</label>
        <span class="form-control">{{ userId }}</span>
      </b-card-title>
      
      <hr>
      <b-card-title v-if="userPasswordCheck==false">
        <label>비밀번호 확인</label>
        <b-input
          v-model="userPassword"
          type="password"
        ></b-input>
        <b-button @click="toCheck">확인</b-button>
      </b-card-title>


      <div v-if="userPasswordCheck==true">
        <label>비밀번호</label>
        <br>
        <b-input 
          v-validate="{ required: true, regex: '^(?=.*[a-z])(?=.*[A-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+]).{8,16}$' }"
          :class="{ input: true, 'is-danger': errors.has('userPassword') }"
          v-model="userPassword"
          name="userPassword"
          type="password"
          placeholder="패스워드"
          ref="userPassword"
        />
        <span v-show="errors.has('userPassword')" style="color: red;">패스워드를 다시 입력하시오.</span>

        <b-input
        v-validate="'required|confirmed:userPassword'"
        :class="{'is-danger': errors.has('userPasswordChk')}" data-vv-as="userPasswordChk"
        v-model="userPasswordChk"
        name="userPasswordChk"
        type="password"
        placeholder="패스워드 확인"
        />
        <span v-show="errors.has('userPasswordChk')" style="color: red;">패스워드가 다릅니다.</span>
      </div>

      <!-- 이름 변경 : 한글 영어 3자이상 10자이하 -->
      <hr>
      <label>이름</label>
      <div class="form-control" v-if="userNameOn==1" >{{ userName }}</div>
      <b-input 
        v-if="userNameOn==0"
        v-validate="{ required: true, regex: '^(?=.*[a-zA-z]).{3,10}$|(?=.*[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]).{3,10}$' }"
        :class="{ input: true, 'is-danger': errors.has('userName') }"
        type="text"
        v-model="userName"
        @input="changeName"
        name="userName"
        placeholder="이름"
        :disabled="userPasswordCheck==0"
      ></b-input>
      <span v-show="errors.has('userName')" style="color: red;">이름을 다시 입력하시오.</span> <br>
      <b-button :disabled="userPasswordCheck==0" :pressed.sync="userNameOn" > 이름 수정</b-button>

      <!-- 닉네임 변경 : 한글 영어 숫자 2자이상 12자이하 -->
      <hr>
      <label>닉네임</label>
      <div class="form-control" v-if="userNicknameOn==1">{{ userNickname }}</div>
      <b-input 
        v-if="userNicknameOn==0"
        v-validate="{ required: true, regex: '((?=.*[a-zA-z])|(?=.*[ㄱ-ㅎ|ㅏ-ㅣ|가-힣])|(?=.*[0-9])).{2,12}$' }"
        v-model="userNickname"
        placeholder="닉네임"
        name="userNickname"
      ></b-input>
      <span v-show="errors.has('userNickname')" style="color: red;">잘못된 양식입니다.</span> <br>
      <b-button :disabled="userPasswordCheck==0" :pressed.sync="userNicknameOn">닉네임 수정</b-button>

      <!-- 이메일 변경 : 이메일 형식에 맞게 [a]@[a].com -->
      <hr>
      <label>이메일</label>
      <div class="form-control" v-if="userEmailOn==1">{{ userEmail }}</div>
      <b-input 
        v-if="userEmailOn==0"
        v-validate="'required|email'"
        :class="{ input: true, 'is-danger': errors.has('userEmail') }"
        v-model="userEmail"
        name="userEmail"
        type="text"
        placeholder="이메일"
      ></b-input>
      <span v-show="errors.has('userEmail')" style="color: red;">잘못된 양식입니다.</span>
      <b-button :disabled="userPasswordCheck==0" :pressed.sync="userEmailOn">이메일 수정</b-button>


      <hr>
      <label>전화번호</label>
      <div class="form-control" v-if="userPhonenumberOn==1">{{ userPhonenumber }}</div>
      <b-input 
        v-if="userPhonenumberOn==0"
        v-validate="{ required: true, regex: '^([0-1]{3}-[0-9]{4}-[0-9]{4})$' }"
        v-model="userPhonenumber"
        placeholder="전화번호"
        name="userPhonenumber"
      ></b-input>
      <span v-show="errors.has('userPhonenumber')" style="color: red;">전화번호 양식이 아닙니다.(xxx-xxxx-xxxx)</span><br>
      <b-button :disabled="userPasswordCheck==0" :pressed.sync="userPhonenumberOn">전화번호 수정</b-button>
      <hr>

      <b-card-title>권한</b-card-title>
      <b-form-radio v-model="userPermission" :aria-describedby="ariaDescribedby" name="some-radios" value=1 v-if="$store.state.userpermission==1">관리자</b-form-radio>
      <b-form-radio v-model="userPermission" :aria-describedby="ariaDescribedby" name="some-radios" value=0 v-if="$store.state.userpermission==1 | $store.state.userpermission==0">사용자</b-form-radio>

      <hr>
      <b-button :disabled="userPasswordCheck==0" class="me-4" type="submit" @click="userUpdate">수정</b-button>
      <br>
      <br>
      <!-- 회원 탈퇴는 관리자만 가능 -->
      <b-card-text @click="toDelete" v-if="$store.state.userpermission == 1">회원 탈퇴</b-card-text> 
      
    </b-card> 
    
  </div>

</template>

<script>
// 유저가 접근하는 것
// 관리자가 접근하는 것

export default {
  data() {
    return {
      user: {},
      id: this.$route.params.id || this.$store.state.userindex,

      update: null,

      userName: null,
      userEmail: null,
      userId: null,
      userNickname: null,
      userPhonenumber: null,
      userPassword: null,
      userPermission: null,
      userPasswordChk: null,

      userPasswordCheck: false,

      userPasswordOn: true,
      userNameOn: true,
      userEmailOn: true,
      userNicknameOn: true,
      userPhonenumberOn: true,
    }
  },
  methods: {
    getUser(){
      this.$axios.get("http://100.0.0.157:20200/user/read/" + this.id).then((res) => {
        console.log(res.data.id)
        this.update = res.data.id
        this.user = res.data
        this.userName = res.data.userName
        this.userEmail = res.data.userEmail
        this.userPhonenumber = res.data.userPhonenumber
        this.userPermission = res.data.userPermission
        this.userId = res.data.userId
        this.userNickname = res.data.userNickname
      })
      .catch((error) => {
        console.log(error);
      })
    },

    toCheck() {
      let saveData = {};
        saveData.userId = this.userId;
        saveData.userPassword = this.userPassword;

      this.$axios.post("http://100.0.0.157:20200/user/login", JSON.stringify(saveData), {
        headers: {
          "Content-Type" : `application/json`,
        },
        body: {
          userId : this.userId,
          userPassword : this.userPassword
        }
      })
      .then((res)=> {
        alert(res.data)
        this.userPasswordCheck = res.data;
      })
        .catch((error) => {
        console.log(error);
      })
    },

    userUpdate() {
      this.$validator.validateAll().then((result) => { // 모든 입력값이 오류가 뜨지 않는다면 요청
        if(result) {
          const formData = new FormData();
          formData.append("id", this.user.id)
          formData.append("userPassword" , this.userPassword)
          formData.append("userName" , this.userName)
          formData.append("userPhonenumber" , this.userPhonenumber)
          formData.append("userEmail" , this.userEmail)
          formData.append("userNickname" , this.userNickname)
          formData.append("userPermission", this.userPermission)

          this.$axios.put("http://100.0.0.157:20200/user/update", formData).then((res)=> {

            if (this.$store.state.userpermission == '1') {
              this.$router.go();

            } else if(this.$store.state.userpermission == '0') {
              this.$store.commit('setNickname', this.userNickname);
              this.$store.commit('setPermission', this.userPermission);
              this.$router.go()
            }
          })
        } else {
          alert("다시 입력해주세요")
        }
      })
      
    },

    toUserUpdate() {
      this.$router.push({name: 'userUpdate', params: {index: this.update}})
    },
    toDelete() {
      this.$axios.delete("http://100.0.0.157:20200/user/delete/" + this.id).then((res) => {
        alert("삭제되었습니다.")
        this.$router.push("/userlist")
      })
    },
    changeNameOn(){
      this.userNameOn = 0;
    },
    changeNameOff(){
      this.userNameOn = 1;
    }
  },
  mounted() {
    this.getUser();
  },
};
</script>