<template>

  <div style="text-align: center">
    <h1>Title : {{ board.boardTitle }}</h1>
    <b-card 
      style ="width: 1000px; display:inline-block"
    >
    <b-card-title>제목 : {{ board.boardTitle }} / 시간 : {{ board.boardTime }} / User : {{ board.userNickname }}</b-card-title>
    <hr>
      <b-card-text>
        {{ board.boardContents }}
      </b-card-text>
    </b-card>
    <hr>
    <b-button @click="toUpdate" v-if="$store.state.usernickname == board.userNickname || $store.state.userpermission == 1">수정</b-button>
    <b-button @click="toDelete" v-if="$store.state.usernickname == board.userNickname || $store.state.userpermission == 1">삭제</b-button>
    <b-button @click="toBack">게시판 목록</b-button>
  </div>

</template>

<script>

export default {
  data() {
    return {
      board: {},
      id: this.$route.params.id || this.$store.state.boardid
    }
  },
  methods: {
    getBoard() {
      this.$axios.get("http://100.0.0.157:20200/board/read/" + this.id).then((res) => {
        console.log(res.data)
        this.board= res.data

      })
      .catch((error) => {
        console.log(error);
      })
    },

    toDelete() {
      this.$axios.delete("http://100.0.0.157:20200/board/delete/" + this.id).then((res) => {
        alert("삭제되었습니다.")
        this.$router.push("/boardlist")
      })
    },

    toUpdate() {
      this.$router.push({name: 'boardUpdate', params: {id: this.id}})
    },
    
    toBack(){
      this.$router.go(-1)
    }

  },

  mounted() {
    this.getBoard();
  },
};
</script>