<template>
  
  <div style="margin-top: 100px; margin-left: 5%; margin-right: 5%; border: 1px solid lightgray; border-radius: 10px; text-align: center">
		<h1>회원 목록</h1>

		<div style=" width: 300px; display:inline-block">
			
			<b-form-input
				id="filter-input"
				v-model="filter"
				type="search"
				placeholder="검색"
			/>
		</div>

		<br>
		<b-table 
			striped hover 
			:items="userList"
			:per-page="perPage"
			:current-page="currentPage"
			:fields="fields"
			sort-icon-left
			@row-clicked="toUser"
			:filter="filter"
			>
			<template #cell(index)="data">
				<router-link :to="{name: 'userInfo', params: {id: data.index.id},}">
				</router-link>
				{{ data.index + 1 }}
			</template>
		</b-table>
		<b-pagination
			v-model="currentPage"
			:total-rows="rows"
			:per-page="perPage"
			aria-controls="my-table"
			style="background-color=#C5F8FF"
			></b-pagination>
		<b-button @click ="toCreate">회원 생성</b-button>
  </div>
	
</template>



<script>

export default {
  data() {
		return {
			userList: [],
			perPage: 10,
			currentPage: 1,
			fields: [
        'index',
        {key: 'userId',},
        {key: 'userNickname',},
        {key: 'userEmail',},
				{key: 'userPermission', sortable: true,},
      ],
			filter: '',
		}
  },
  
  methods: {
		user() {
			try {
				this.$axios.get("http://100.0.0.157:20200/user/list")
					.then((res) => {
				if (res.status == 200) {
					this.userList = res.data;
					console.log(res.data)
				} 
				})
			} catch (error) {
				alert("error")
			}
		},
		toCreate() {
			this.$router.push("/boardcreate");
		},
		toUser(index) {
      this.$router.push({name: 'userInfo', params: {id: index.id}})
    },
		toCreate(){
			this.$router.push({name: 'signUp'})
		}
	},
	created() {
		this.user();
	},

	computed: {
		rows() {
			return this.userList.length;
		},
  }
}
</script>